import coinsMock from '@/store/mock/coinsMock'

export default coinsMock.map(el => {
  const coin = {
    ...el,
    title: el.title === 'Perfect Money' ? 'PM' : el.title
  }
  return {
    coin: coin,
    percents: '+11,56%',
    profit: '+$180,131.00',
    rate: '56.7239' + coin.title,
    amount: '$180,131.00'
  }
})
